nimation-name:tap-upper-1}.preloader__obj1:nth-child(2){-webkit-animation-delay:var(--tap-stagger);animation-delay:var(--tap-stagger);-webkit-filter:brightness(80%);filter:brightness(80%);-webkit-animation-name:tap-upper-2;animation-name:tap-upper-2}.preloader__obj1:nth-child(3){-webkit-animation-delay:calc(var(--tap-stagger) * 2);animation-delay:calc(var(--tap-stagger) * 2);-webkit-filter:brightness(90%);filter:brightness(90%);-webkit-animation-name:tap-upper-3;animation-name:tap-upper-3}.preloader__obj1:nth-child(4){-webkit-animation-delay:calc(var(--tap-stagger) * 3);animation-delay:calc(var(--tap-stagger) * 3);-webkit-filter:brightness(100%);filter:brightness(100%);-webkit-animation-name:tap-upper-4;animation-name:tap-upper-4}@-webkit-keyframes tap-upper-1{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.4);transform:rotate(10deg) scale(0.4)}40%{-webkit-transform:rotate(50deg) scale(0.4);transform:rotate(50deg) scale(0.4)}}@keyframes tap-upper-1{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.4);transform:rotate(10deg) scale(0.4)}40%{-webkit-transform:rotate(50deg) scale(0.4);transform:rotate(50deg) scale(0.4)}}@-webkit-keyframes tap-upper-2{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.6);transform:rotate(10deg) scale(0.6)}40%{-webkit-transform:rotate(50deg) scale(0.6);transform:rotate(50deg) scale(0.6)}}@keyframes tap-upper-2{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.6);transform:rotate(10deg) scale(0.6)}40%{-webkit-transform:rotate(50deg) scale(0.6);transform:rotate(50deg) scale(0.6)}}@-webkit-keyframes tap-upper-3{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.8);transform:rotate(10deg) scale(0.8)}40%{-webkit-transform:rotate(50deg) scale(0.8);transform:rotate(50deg) scale(0.8)}}@keyframes tap-upper-3{0%,50%,100%{-webkit-transform:rotate(10deg) scale(0.8);transform:rotate(10deg) scale(0.8)}40%{-webkit-transform:rotate(50deg) scale(0.8);transform:rotate(50deg) scale(0.8)}}@-webkit-keyframes tap-upper-4{0%,50%,100%{-webkit-transform